@import url(https://fonts.googleapis.com/css?family=Courgette|Roboto:400,700" rel="stylesheet);
@import url(https://use.fontawesome.com/releases/v5.8.2/css/all.css);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif !important; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.text-slanted {
  font-family: 'Courgette', cursive;
}

h1 {
  font-size: 24px !important;
  font-weight: 500 !important;
  margin: 20px 0 !important;
}

p {
  line-height: 26px;
}

/* styles */
.navbar {
  background-color: #2F96F3!important;
}
.navbar-light .navbar-brand {
  color: #fff !important;
}
.navbar-header-styles {
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.3);
}
.wrapper {
  width: 300px;
  position: fixed;
  top: 56px;
  height: 100%;
  background: #f8f9fa;
  left: -300px;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
  z-index: 99;
  box-shadow: 3px 10px 10px #6c757d;
}
.wrapper.active {
  left: 0;
}
.page-wrap {
  margin-left: 300px;
  width: calc(100% - 300px);
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
  margin-top: 56px;
}
.page-wrap.inactive {
  margin-left: 0;
  width: 100%;
}
.toggle-btn {
  display: none;
}
.toggle-btn span {
  width: 30px;
  height: 4px;
  display: block;
  margin: 5px 0;
  background: #fff;
}
.alert-is-helpful {
  border-left: 8px solid #2F96F3;
  padding: 16px;
  margin: 24px 0;
  background: rgba(25,118,210,.05);
}
.callout-is-helpful {
  margin: 24px 0;
}
.alert-is-helpful p, 
.callout-is-helpful p {
  margin: 0;
}
.callout-is-helpful .card-header {
  background: #2F96F3;
}
.callout-is-helpful .card-body {
  background: rgba(25,118,210,.05);
}
.callout-is-helpful .card-header {
  margin: 0;
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.5px;
}
pre {
  margin: 24px 0 !important;
}
footer {
  position: fixed;
  bottom: 0;
  width: 300px;
  background: #2F96F3;
  color: #fff;
  padding: 8px 16px;
}
footer p {
  margin: 0;
}
@media screen and (max-width: 991px) {
  .toggle-btn {
    display: block;
  }
  .page-wrap {
    margin-left: 0;
    width: calc(100% - 50px);
  }
}
@media screen and (min-width: 992px) {
  .wrapper {
    margin-left: 0;
    left: 0;
  }
  .page-wrap {
    margin-left: 320px;
    width: calc(100% - 500px);
  }
}




#sidebar .navbar {
  flex-direction: column !important;
  align-items: flex-start;
  background-color: #f8f9fa!important;
}

#sidebar .navbar-nav {
  margin: 0 0 16px 0 !important;
  padding: 0;
  flex-direction: column !important;
  width: 100%;
}

#sidebar .navbar-brand {
  color: #2F96F3 !important;
  text-transform: uppercase;
  font-size: 16px;
  padding: 4px 0 4px;
  letter-spacing: 0.5px;
  font-weight: 500;
}

#sidebar .navbar-nav li {
  padding: 8px 0 0 0;
}

/* #sidebar .navbar-nav li:last-child {
  border-bottom: none;
  padding: 12px 0 2px;
} */

#sidebar .navbar-nav li a {
  color: #343a40;
  padding: 4px 0 4px 10px
}

#sidebar .navbar-nav li a:hover {
  text-decoration: none;
}

.close-btn {
  width: 100%;
  text-align: right;
}

@media screen and (min-width: 992px) {
  .close-btn {
    display: none;
  }
}
